import React, {useEffect,useState} from 'react';
import './App.css';
import axios from 'axios';
import Coin from './Coin';


function App() {
   const [coins,setCoins] = useState([]);
   const [search,setSearch] = useState('');

  useEffect(() => {
    const intervalid = setInterval (() => {
    axios
    .get("https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1&sparkline=true")
    .then(res => {
       setCoins(res.data);
       console.log(res.data);
    }).catch( error => alert("Error"))
  },1000)
  return () => clearInterval(intervalid)

  
  })

  const handleChange = (e) =>{
    setSearch(e.target.value);
  }

  const filteredCoins = coins.filter(coin =>
    coin.name.toLowerCase().includes(search.toLowerCase())
    )

 

  return (
    <div className="coin-app">
      <div className="coin-search">
      <h1 className="main-text">Crypto Finance Tracker</h1>
        <h1 className="search-text">Search Coin</h1>
        <form>
          <input type="search" placeholder="Search" 
          className="coin-input" onChange={handleChange}>
          </input>
        </form>
      </div>
      {filteredCoins.map( coin => {
        return(
          <Coin 
           key={coin.id}
           name={coin.name}
           image={coin.image}
           symbol={coin.symbol}
           marketcap={coin.market_cap}
           price={coin.current_price}
           priceChange={coin.price_change_percentage_24h}
           volume={coin.total_volume}
          />
        )
     })}
    </div>
  );
}

export default App;
